.form {
  color: rgba(5, 38, 48, .757);
  align-items: center;
  gap: 20px;
  padding-left: 30px;
  font-weight: 700;
  display: flex;
}

.delay {
  height: 40px;
  max-width: 120px;
  border: 3px solid rgba(8, 76, 90, .757);
  border-radius: 10px;
  font-size: 18px;
  transition-property: outline;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.delay:focus, .delay:hover {
  outline-offset: -3px;
  outline: 3px solid rgba(16, 45, 60, .757);
}

.step {
  height: 40px;
  max-width: 120px;
  border: 3px solid rgba(8, 76, 90, .757);
  border-radius: 10px;
  font-size: 18px;
  transition-property: outline;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.step:focus, .step:hover {
  outline-offset: -3px;
  outline: 3px solid rgba(16, 45, 60, .757);
}

.amount {
  height: 40px;
  max-width: 120px;
  border: 3px solid rgba(8, 76, 90, .757);
  border-radius: 10px;
  font-size: 18px;
  transition-property: outline;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.amount:focus, .amount:hover {
  outline-offset: -3px;
  outline: 3px solid rgba(16, 45, 60, .757);
}

.btn {
  height: 50px;
  max-width: 120px;
  cursor: pointer;
  color: rgba(8, 76, 90, .757);
  background-color: #fff;
  border: 2px solid rgba(8, 76, 90, .757);
  border-radius: 15px;
  font-size: 16px;
  font-weight: 700;
  transition-property: border, color, background-color, box-shadow;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.btn:hover, .btn:focus {
  color: #fff;
  background-color: rgba(8, 76, 90, .757);
  border: 2px solid rgba(33, 79, 102, .757);
  font-weight: 700;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

